.Link {
    display: inline;
    height: auto;
    width: 92%;
    margin: 2px;
}

.Links {
    height: 40px;
}

.Content {
}

.Tile {
    flex: 0 0 29%;
    padding: 20px;
}

Img {
    max-width: 100%;
    max-height: 100%;
}

.project-icon {
    margin-right: 4px;
    height: 20px;
}

.content-modal {
	position: fixed;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

.repo-link {
    height: 20px;
}

ul {
    list-style: none;
    text-align: center;
    padding: 0px;
    margin: 0px;
}
li {
    display: inline-block;
    margin: 0 8px;
}
li::after {
    content: ' \2022';
}

.image-container {
    position: relative;
    margin: 10px;
}
.image-container .after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    color: #000;
}
.image-container:hover .after {
    display: block;
    background: rgba(63, 114, 175, 0.3);
}

.Contents {
}

.Tiles {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

#div-left {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}

#div-right {
    text-align: center;
}

#div-notice {
    text-align: center;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

